export default {
	data() {
		return {
			lbt : [],
			shop:[],
			ard:this.ard,
			url:this.url1
		}
	},
	onLoad(options) {
		this.getArr()
	},
	onShow() {
		this.lunBo.forEach(
		(item,i)=>{
			console.log(item.bannername)
			if(item.bannername=='家纺'){
				this.lbt=item.banneradress
				}
			}
		)
	},
	methods: {
		goShopping:function(a){
			let that=this;
			let usurl2=that.url1+"/dinghai/shop/byid";
			uni.showLoading({
				title: '正在获取',
				mask: true
			});
			uni.request({
			    url: usurl2,
				header:{
					"Content-Type" : "application/x-www-form-urlencoded"
				},
				method:'GET',
			    data: {
					uid:a.uid
				},
			    success: (res) => {
					if(res.statusCode==200){
						uni.showToast({
							title: '加载成功'
						});
						console.log(res)
						uni.navigateTo({
							url:"../goShopping/goShopping?goShopping="+JSON.stringify(a)+'&shop='+JSON.stringify(res.data.data)
						})
					}else{
						uni.showToast({
							title: '您的网络打了个盹！',
							icon:"none"
						});
					}
			    },
				fail : function(err){
					console.log(err)
					uni.showToast({
						title: '您的网络打了个盹！',
						icon:"none"
					});
				}
			})
		},
		go:function(item){
			uni.navigateTo({
				url:"../dianPu/dianPu?item="+JSON.stringify(item)
			})
		},
		getArr:function () {
			let that=this;
			let usurl=that.url1+"/dinghai/commodity/getbytypeall";
			uni.showLoading({
				title:'正在加载',mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							type:'家纺',
							start:1,
							size:20
						},
				    success: (res) => {
						console.log(res)
						if(res.data.code==200){
							that.shop=res.data.data;
							for(let i=0;i<that.shop.length;i++){
								that.shop[i].img1=JSON.parse(that.shop[i].img1);
								that.shop[i].img2=JSON.parse(that.shop[i].img2);
								// that.shop[i].shopType=JSON.parse(that.shop[i].shopType);
								// that.shop[i].label=JSON.parse(that.shop[i].label)
							}
							uni.showToast({
								title:"加载成功",
								icon:"none"
							})
						}else{
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
		back : function(){
		   uni.navigateBack({

		   });
		},
		
	}
}
